<template>
    <div class="container">
     

        <div  class="navigation">
            <ul v-show="!mobile" class="navigation_ul">
                <li  class="nav-item"  v-for="item in navigation" :key="item">
                            <router-link class="nav-link  active" aria-current="page" :to="item.path">
                            {{item.name}}
                            </router-link>
                </li>
            </ul>
            
            <router-link to="/lyma/"><img class="logo" src="../assets/Frame.png" alt=""></router-link>
            <ul v-show="!mobile" class="menuList">
                <li  class="menulist-item"  v-for="item in menuList1" :key="item">
                            <router-link class="nav-link  active" aria-current="page" :to="item.path">
                            {{item.name}}
                            </router-link>
                </li>
                
                    <button class="buy" type="">BUY</button>
                
            </ul>
            
        </div>
        <div class="icon">
                <i @click="toggleMobileNav" v-show="mobile" class="fa-solid fa-bars" :class="{'icon-active':mobileNav}"></i>
                
            </div>
            <transition name="mobile-nav">
                <ul v-show="mobileNav" class="dropdown-nav">
                <li  class="nav-item-mobile"  v-for="item in navigation" :key="item">
                            <router-link class="nav-link  active" aria-current="page" :to="item.path">
                            {{item.name}}
                            </router-link>
                </li>
            </ul>
            </transition>
           
    </div>    
            
        
    
</template>
<script>
import {navigation,menuList1} from '../components/constants/menuLinks'

export default {
    props: {
        msg:String
    },
    components: {
        navigation,
        menuList1
    },
    data() {
        return {
            navigation:navigation,
            menuList1:menuList1,
            mobile: null,
            mobileNav:null,
            
        }
    },
    created() {
        window.addEventListener('resize',this.checkScreen);
        this.checkScreen();
    },
    methods: {
        toggleMobileNav () {
            this.mobileNav = !this.mobileNav;
        },
        checkScreen () {
            this.windowWidth = window.innerWidth;
            if(this.windowWidth <= 900) {
                this.mobile = true;
                return;
            }
            this.mobile = false;
            this.mobileNav = false;
            return;
        }
    },
    
   
    
        
    
}

</script>

<style>


.navigation {
    height: 65px;
    display: flex;
    width: 100%;
    justify-content: space-between;
    position: fixed;
    z-index: 10;
    background-color: rgba(255, 255, 255, 0.436);
    top: 0;
    flex-shrink: 1;
    transition: 0.5s ease all;
}

ul,li,a {
    list-style: none;
    display: flex;
    text-decoration: none;
    align-self: center;
    
}
@media (max-width:900px) {
    .navigation_ul{
        padding: 10px;
       
    }
}
li {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 10px;
    line-height: 12px;
    letter-spacing: 1.67px;
    color: rgba(0, 0, 0, 0.3);
    margin-right: 32px;
}
@media (max-width:1000px) {
    li{
        
        margin-right: 20px;
    }
}
@media (max-width:910px) {
    li{
        
        margin-right: 15px;
    }
}
.nav-link {
    color: rgba(0, 0, 0, 0.3);
}
.logo {
    height: 12px;
    align-self: center;
    margin-left: 168px;
    margin-right: 192px;
}
@media (max-width:1470px) {
    .logo{
        margin-left: 5%;
        margin-right: 5%;
    }
}
.buy {
    align-self: center;
    display: block;
    width: 64px;
    height: 23px;
    background-color: #000000;
    color: #FFFFFF;
    line-height: 12px;
    font-size: 10px;
    letter-spacing: 1.67px;
    margin-right: 40px;
}
.dropdown-nav {
    display: block;
    height: 100%;
    width: 100%;
    background-color: #fff;
    margin-top: 60px;
}
.nav-item-mobile {
    margin-bottom: 20px;
    justify-content: center;
}
i {
    
    color: rgb(0, 0, 0);
}
.fa-bars::before {
   
    background-color: #0000006d;
    
}
.icon {
    position: absolute;
    top: 10px;
    align-items: center;
    right: 24px;
    cursor: pointer;
    z-index: 12;
}
.dropdown-nav {
    display: flex;
    flex-direction: column;
    position: fixed;
    height: 100%;

}
</style>