<template>
    <div class="carousel">
        <slot :currentSlide="currentSlide"/>
         <!--Navigation -->
        <div class="navigate container">
            <div class="toggle-page left">
                <i @click="prevSlide" class="fa-solid fa-chevron-left"></i>
            </div>
            <div class="toggle-page right">
                <i @click="nextSlide" class="fa-solid fa-chevron-right"></i>
            </div>
        </div>
        <!--Pagination -->
        <div class="pagination">
            <span @click="goToSlide(index)" v-for="(slide,index) in getSlideCount" :key="index" :class="{activeSlide:index === currentSlide - 1}">
                
            </span>
        </div>
    </div>

   
    

</template>
<script>
import {ref, onMounted} from 'vue';


export default {
    setup() {
        const currentSlide = ref(1);
        const getSlideCount = ref(null);

        //next slide 
        const nextSlide = () => {
            if(currentSlide.value === getSlideCount.value) {
                currentSlide.value = 1;
                return;
            }
            currentSlide.value +=1;
        }
        //prev slide 
        const prevSlide = () => {
            if(currentSlide.value === 0) {
                currentSlide.value = 1;
                return;
            }
            currentSlide.value -=1;
        }
        const goToSlide = (index) => {
            currentSlide.value = index + 1;
        }
        onMounted(() => {
            getSlideCount.value = document.querySelectorAll('.slide').length;
            console.log(getSlideCount.value);
        })

        return {currentSlide,getSlideCount,nextSlide, prevSlide,goToSlide};

    }

}

</script>
<style>
.navigate {
    padding: 0 16px;
    height: 100%;
    width: 100%;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
}

.toggle-page {
    display: flex;
    flex: 1;
}
.right {
    justify-content: flex-end;
    margin-right: 15px;
   
}
i::before {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    background-color: #6347c7;
    color: #fff;
}
.pagination {
    position: absolute;
    bottom: 24px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap:16px;
}

span {
    cursor: pointer;
    width: 10px;
    height: 10px;
    
    background-color: #AAAAAA;
}
.activeSlide {
    background-color: #1B1B1B;
}
</style>